html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}
html {
   font-size: 100%;
   height: 100%;
   padding: 0;
   margin: 0;
   -webkit-text-size-adjust: 100%;
       -ms-text-size-adjust: 100%;
   font-family: sans-serif;
}
body {
    line-height: 1;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    position: relative;
}
ol, ul {
    list-style: none;
}
dl,
menu,
ol,
ul {
    margin: 1em 0;
}
dd {
    margin: 0 0 0 40px;
}
menu,
ol,
ul {
    padding: 0 0 0 40px;
}
blockquote, q {
    quotes: none;
}

blockquote {
    margin: 1em 40px;
    &:before, &:after {
        content: '';
        content: none;
    }
}
q {
    quotes: none;
    &:before,
    &:after {
        content: '';
        content: none;
    }
}
a {
    &:focus {
        outline: thin dotted;
    }
    &:focus,
    &:active,
    &:hover {
        outline: 0;
    }
}
h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
}
b, strong {
    font-weight: bold;
}
p, pre {
    margin: 0 0 18px;
}
code, kbd, pre, samp {
    font-family: monospace, serif;
    _font-family: 'courier new',monospace;
    font-size: 1em;
}
pre {
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
}
button, input, select, textarea {
    font-family: sans-serif;
    font-size: 100%;
    margin: 0;
    vertical-align: baseline;
    *vertical-align: middle;
    line-height: normal;
}
textarea {
    overflow: auto;
    vertical-align: top;
}
button,
html input[type='button'] {
    -webkit-appearance: button;
    cursor: pointer;
    *overflow: visible;
}

input {
    &[type='reset'],
    &[type='submit'] {
        -webkit-appearance: button;
        cursor: pointer;
        *overflow: visible;
    }
}
button[disabled] {
   cursor: default;
}
input {
   &[disabled] {
      cursor: default;
   }
   &[type='checkbox'],
   &[type='radio'] {
      box-sizing: border-box;
      padding: 0;
      *height: 13px;
      *width: 13px;
   }
   &[type='search'] {
      box-sizing: content-box;
      &::-webkit-search-cancel-button,
      &::-webkit-search-decoration {
         -webkit-appearance: none;
      }
   }
}
button::-moz-focus-inner,
input::-moz-focus-inner {
   border: 0;
   padding: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    &:after,
    &:before {
        box-sizing: border-box;
        font-smoothing: antialiased;
        text-rendering: optimizeLegibility;
    }
}
dfn {
    font-style: italic;
}
mark {
    background-color: #ff0;
    color: #000;
}
audio, canvas, video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
}
audio:not([controls]) {
   display: none;
   height: 0;
}
[hidden] {
   display: none;
}
abbr[title] {
   border-bottom: 1px dotted;
}
small {
   font-size: 80%;
}

sub {
   font-size: 75%;
   line-height: 0;
   position: relative;
   vertical-align: baseline;
}
sup {
   font-size: 75%;
   line-height: 0;
   position: relative;
   vertical-align: baseline;
   top: -.5em;
}
sub {
   bottom: -.25em;
}
nav {
   ul, ol {
      list-style: none;
      list-style-image: none;
   }
}

img {
   border: 0;
   max-width: 100%;
   vertical-align: bottom;
   -ms-interpolation-mode: bicubic;
}

svg:not(:root) {
   overflow: hidden;
}
figure, form {
   margin: 0;
}
fieldset {
   margin: 0;
   padding: 0;
   border: 0;
}
legend {
   border: 0;
   padding: 0;
   white-space: normal;
   *margin-left: -7px;
}
.clearfix {
   &:before {
      display: table;
      content: '';
   }
   &:after {
      display: table;
      content: '';
      clear: both;
   }
}
.clear {
   clear: both;
}
.clearfix {
   *zoom: 1;
}